<template>
  <el-select v-model="checked"  @change="selectChange" filterable  placeholder="请选择对应的分项考核" class="tail-end-input">
    <el-option
        v-for="item in selectOptions"
        :key="item.examineId"
        :label="item.name"
        :value="item.examineId"
        :disabled="item.disabled"
        ></el-option>
   </el-select>
</template>

<script>
export default {
 name: 'MySelect',
 props: {
    value:{
       type:String,
       default:''
   },
   options:{
       type:Array,
       default(){
           return []
       }
   },
   parentIndex:{
       type:Number,
       default:0
   },
   childIndex:{
       type:Number,
       default:0
   }
 },
 data() {
 return {
   selectOptions:[],
   checked:''
 }
 },
 created(){},
 mounted(){
     this.checked=this.value 
     this.selectOptions=this.options
 },
 watch:{
    value(curVal){
       this.checked=curVal
    },
    options(curVal){
       this.selectOptions=curVal
    },
 },
 methods:{
    selectChange(){
        this.$emit('onChange',  this.parentIndex, this.childIndex, this.checked)
    }
 }
}
</script>

<style lang='scss' scoped>

</style>



