<template>
 <div class="course-construction">
     <title-curmb parentMate="试卷配置" mate="配置考查内容与课程目标对应关系"></title-curmb>
     <div class="relation_title">《{{courseName}}》课程试卷配置</div>
     <div class="course-construction_body">
         <el-form :model="form" :rules="rules" ref="form" label-width="0">
            <h2>分项考核及考查内容</h2>
            <el-row>
                <el-form-item  prop="patternOptions">
                    <exam-content
                    v-for="(vo, index) in patternOptions" 
                    :key="index"
                    :data="vo"
                    :indexVal="index"
                    @onChange="contentItemChange"
                    @onDelete="contentItemDel"
                    >
                    </exam-content>
                   </el-form-item>
                 <div @click="addpatternOptions"  class="add-step-title add-step-button"><SvgIcon icon-class="icontianjia" size="20px"></SvgIcon><span>新增考核方式</span></div>
                 <div @click="saveCreate('form')" class="show-button" style="width:200px;">
                     <SvgIcon icon-class="iconxiangxiajiantou" size="12px"></SvgIcon>
                     &nbsp;保存数据并填入下表
                 </div>
            </el-row>
            <el-form-item  prop="tableData" ref="formTable">
                <el-row v-for="(vo, index) in tableData" :key="index">
                    <h2>{{vo.examGoalName}}：分项考核权重及考查内容</h2>
                        <el-table :data="vo.table" border class="cc-table" style="width: 100%;">
                            <el-table-column type="index" label="编号" align="center" width="50">
                            </el-table-column>
                            <el-table-column label="分项考核" width="200">
                                <template slot-scope="scope">
                                    <my-select  
                                        ref="mySelect"
                                        :options="vo.patternOptions" 
                                        :value="scope.row.examineId" 
                                        :parentIndex="index"
                                        :childIndex="scope.$index"
                                        @onChange="mySelectChange">
                                    </my-select>
                                </template>
                            </el-table-column>
                            <el-table-column label="权重" width="200">
                                <template slot-scope="scope">
                                    <el-input-number v-model="scope.row.ratio"  :min="0" controls-position="right"></el-input-number>
                                </template>
                            </el-table-column>
                            <el-table-column label="考查内容">
                                <template slot-scope="scope"> 
                                    <div style="width:100%; height:100%; display: flex;justify-content: center;align-items: center;">
                                        <div v-if="scope.row.checkArr.length" style="width:100%; display: flex;justify-content: space-between;align-items: center;">    
                                            <div style="display: flex;"><div style="margin-right:10px;" v-for="(v, n) in scope.row.checkArr" :key="n"><span class="tick"></span><span style="display:inline-block; transform: translateY(-3px);">{{v.name}}</span></div></div>
                                            <div class="operate-button distance">                 
                                                <el-button
                                                @click.native.prevent="checkRow(index, scope.$index, vo.examGoalName, scope.row)"
                                                type="text"
                                                size="small">
                                                重新选择 >
                                                </el-button>
                                            </div>
                                        </div>
                                        <div @click="checkRow(index, scope.$index, vo.examGoalName, scope.row)"  v-if="scope.row.checkArr.length ===0" class="show-button" style="width:120px;margin-bottom:0;">选择考查内容 ></div>
                                    </div>
                                </template>
                            </el-table-column>
                            <el-table-column label="操作" width="50">
                                <template slot-scope="scope">
                                    <label @click="rowDel(index, scope.$index, scope.row)" class="tail-end_del"><SvgIcon icon-class="iconqudiao" size="20px"></SvgIcon></label>
                                </template>
                            </el-table-column>
                        </el-table>  
                    <el-row>
                         <div @click="addTableRow(index)"  class="add-step-title add-step-button" v-if="vo.table.length < patternOptions.length"><SvgIcon icon-class="icontianjia" size="20px"></SvgIcon><span>添加分项考核</span></div>
                         <span class="tips">* 考核方式权重总和等于1</span>
                    </el-row>       
                </el-row>
             </el-form-item>
            <el-row :gutter="24">
                <el-col :span="6" :offset="9">
                     <div class="c-footer">
                        <div class="submit--btn" @click="formSubmit('form')">提&nbsp;&nbsp;交</div>
                    </div>
                </el-col>
            </el-row>
       </el-form>
     </div>
     <examine-dialog ref="examineDialog" @success="dialogChange"></examine-dialog>
 </div>
</template>

<script>
import TitleCurmb from './components/TitleCurmb'
import ExamContent from './components/ExamContent'
import MySelect from './components/MySelect'
import ExamineDialog from './components/ExamineDialog'

var floatObj = function() {
    function isInteger(obj) {
        return Math.floor(obj) === obj
    }
    function toInteger(floatNum) {
        var ret = {times: 1, num: 0}
        if (isInteger(floatNum)) {
            ret.num = floatNum
            return ret
        }
        var strfi  = floatNum + ''
        var dotPos = strfi.indexOf('.')
        var len    = strfi.substr(dotPos+1).length
        var times  = Math.pow(10, len)
        var intNum = parseInt(floatNum * times + 0.5, 10)
        ret.times  = times
        ret.num    = intNum
        return ret
    }
    function operation(a, b, op) {
        var o1 = toInteger(a)
        var o2 = toInteger(b)
        var n1 = o1.num
        var n2 = o2.num
        var t1 = o1.times
        var t2 = o2.times
        var max = t1 > t2 ? t1 : t2
        var result = null
        switch (op) {
            case 'add':
                if (t1 === t2) { // 两个小数位数相同
                    result = n1 + n2
                } else if (t1 > t2) { // o1 小数位 大于 o2
                    result = n1 + n2 * (t1 / t2)
                } else { // o1 小数位 小于 o2
                    result = n1 * (t2 / t1) + n2
                }
                return result / max
            case 'subtract':
                if (t1 === t2) {
                    result = n1 - n2
                } else if (t1 > t2) {
                    result = n1 - n2 * (t1 / t2)
                } else {
                    result = n1 * (t2 / t1) - n2
                }
                return result / max
            case 'multiply':
                result = (n1 * n2) / (t1 * t2)
                return result
            case 'divide':
                result = (n1 / n2) * (t2 / t1)
                return result
        }

    }
    // 加减乘除的四个接口
    function add(a, b) {
        return operation(a, b, 'add')
    }
    function subtract(a, b) {
        return operation(a, b, 'subtract')
    }
    function multiply(a, b) {
        return operation(a, b, 'multiply')
    }
    function divide(a, b) {
        return operation(a, b, 'divide')
    }
    return {
        add: add,
        subtract: subtract,
        multiply: multiply,
        divide: divide
    }
}();

export default {
 name: 'ToconfigureHb',
 components: {TitleCurmb, ExamContent, MySelect, ExamineDialog},
 data() {
   const checkpatternOptions = (rule, value, callback) => {
        let num=0
        if(this.patternOptions.length ===0){
            callback(new Error('请添加分项考核及考查内容'))
        }
        this.patternOptions.forEach(vo =>{
            if(vo.name === '' || vo.examContentnames === ''){
                num ++
            }
        })
        if(num ===0){
           callback()
        }else{
           callback(new Error('分项考核及考查内容不能有空'))
        }
    };
    const checkTableData = (rule, value, callback) => {
         if(this.tableData.length ===0){
            callback(new Error('请添加课程目标的分项考核权重及考查内容'))
        }
        let nameNum=0
        let contentNum=0
        let tableNum=0
        this.tableData.forEach(item =>{
            if(item.table.length === 0){
                 tableNum+=1
            }
            item.table.forEach(vo =>{
                if(vo.examineId === ''){
                    nameNum+=1
                }
                if(vo.checkArr.length === 0){
                    contentNum+=1
                }
            })
        })

         if(tableNum !==0){
            callback(new Error('每个课程目标必须设置分项考核权重及考查内容'))
        }
        if(nameNum !==0){
            callback(new Error('每个课程目标下的分项考核不能为空'))
        }
        if(contentNum !==0){
            callback(new Error('每个课程目标下的考查内容不能为空'))
        }
        function recursiveCheck(arr) {
            let sum = 0;
            for (let vo of arr) {
                const ratio= vo.ratio || 0
                sum=floatObj.add(sum, ratio)
            }
            if (sum === 1 ) {
                return "next";
            } else {
                return callback(new Error("每个课程目标下的权重总和必须等于1"));
            }
        }
        for (let i = 0, length = this.tableData.length; i < length; i++) {
            if (recursiveCheck(this.tableData[i].table) === "next") {
                if (this.tableData[i + 1]) {
                    recursiveCheck(this.tableData[i + 1].table);
                } else {
                    callback();
                    break;
                }
            }
        }
    };
 return {
    courseName: this.$route.query.name,
    courseId:this.$route.query.id || '',
    loadding:false,
    patternOptions:[],
    initTableData:[],
    tableData:[],
    form:{
      patternOptions:[],
      tableData:[]
    },
    rules:{
        patternOptions: [{ required: true, validator: checkpatternOptions, trigger: "blur"}], 
        tableData: [{ required: true, validator: checkTableData, trigger: "no"}]
    },
    api:{
         details:courseId=>`/basic/traExamContent/getexamine/${courseId}`, //详情
         getId:() =>`/basic/courseGoal/getId`, //获取动态ID
         save:() => `/basic/traExamContent/PattentContentBindGoal`
    }
 }
 },
 created(){
   this.initData()
 },
 mounted(){
  
 },
 methods:{
      initData(){
        const url=this.api.details(this.courseId)
        this.$get(url).then(res =>{
            if(res.status ===1){     
              const patternList=res.data.patternList || []
              const patternGoalList=res.data.patternGoalList || []
              const examineArr=[]
              const goalArr=[]
              //考查内容数据
              patternList.forEach(item =>{
                  const arr=item.names.split(',')
                  const newArr=[]
                  arr.forEach(vo =>{ if(vo){newArr.push({name : vo})}})
                  const obj={
                        name:item.examine_name,
                        examineId:item.examine_id,
                        disabled:false,
                        examContentnames:item.names,
                        examContentArr:newArr
                  }
                  examineArr.push(obj)
              })
              //课程目标对应关系数据
              patternGoalList.forEach(item =>{
                 const goalObj= {
                        examGoalName:item.name,
                        examGoalId:item.id,
                        patternOptions:[],
                        table:[]
                    }
                    item.examines.forEach(vo =>{
                        goalObj.table.push({
                            examineId:'', 
                            ratio: 0,
                            showArr:[],
                            checkArr:[]
                        })
                    })
                    goalArr.push(goalObj)
              })
         
              this.patternOptions=examineArr
              this.initTableData=goalArr
              this.initPatternGoalList(patternGoalList)
              
            }else{
                this.$notify.error({
                    title: "error",
                    duration: 1500,
                    message: res.message
                });
            }
        })
     },
     //详情返回生成数据
     initPatternGoalList(list){
        const goals=[]
        const patterns=JSON.parse(JSON.stringify(this.patternOptions))
        const patternObj={} //添加每个目标下已经选择的ids
        //数据重组
       list.forEach(item =>{
            const goalsObj= {
                examGoalName:item.name,
                examGoalId:item.id,
                patternOptions: patterns,
                table:[]
            }
            patternObj[`${item.id}`]=[]
            item.examines.forEach(vo =>{
                const examinesArr=vo.names.split(',')
                const newExaminesArr=[]
                examinesArr.forEach(v =>{ if(v){newExaminesArr.push({name : v})}})
                patternObj[`${item.id}`].push(vo.examineId)
                goalsObj.table.push({
                    examineId:vo.examineId, 
                    ratio: + vo.ratio,
                    showArr:[],
                    checkArr:newExaminesArr
                })
            })

            goals.push(goalsObj)
        })
         //左侧下拉是否可选
         /********************************************************** */
         //判断每个目标下的分项下拉项是否可选，并添加到一个新对象里面
         const goalsPatternOptions={}   
           goals.forEach(item =>{
               goalsPatternOptions[`${item.examGoalId}`]=[]
              item.patternOptions.forEach(vo =>{
                  if(patternObj[`${item.examGoalId}`]){  
                        const  patternItem=JSON.parse(JSON.stringify(vo))  
                        patternObj[`${item.examGoalId}`].forEach(id =>{
                            if(patternItem.examineId === id){
                                  patternItem.disabled=true
                            }
                        })

                       goalsPatternOptions[`${item.examGoalId}`].push(patternItem)
                  }

    
            })
        })
  
        //将已经赋完是否可选权限的值替换对应数据
        goals.forEach(item =>{
             if(goalsPatternOptions[`${item.examGoalId}`]){  
                    item.patternOptions=goalsPatternOptions[`${item.examGoalId}`]
                  }
        })
        /********************************************************** */
        //右侧对应弹框展示内容的默认值包括已选为选的
        goals.forEach(item =>{
            item.table.forEach(vo =>{
                 patterns.forEach(v =>{
                     if(vo.examineId ===v.examineId){
                            vo.defaultArr=v.examContentArr
                     }
                 })
            })
        })

        this.tableData=goals
       
     },
     addpatternOptions(){
        this.$get(this.api.getId()).then(res => {
            const obj={
                name:'',
                examineId:res,
                disabled:false,
                examContentnames:'',
                examContentArr:[]
              }
            this.patternOptions.push(obj) 
        });   
    },
    contentItemChange(type, index, newName, newContent){
         this.tableData=[]
        if(type === 'title'){
            this.patternOptions[index].name=newName
        }else{
             const reg = /，/g
             const valStr=newContent.replace(reg, ',')
             const initArr=valStr.split(',')
             const arr= initArr.filter((item, index, Array) =>{
                  return Array.indexOf(item) === index
              })

             this.patternOptions[index].examContentnames=arr.join(',')

             const newArr=[]
             arr.forEach((item, n)=>{
                if(item){
                    newArr.push({ name:item })
                }
                
             })
             this.patternOptions[index].examContentArr= newArr       

        }

    },
    contentItemDel(index){
       this.patternOptions.splice(index, 1)
       this.tableData=[]
    },
    saveCreate(formName){
     this.$refs[formName].validateField("patternOptions", errMsg => {
        if (errMsg !== '请添加分项考核及考查内容' && errMsg !== '分项考核及考查内容不能有空') {
                this.tableData=[]
                this.$nextTick(()=>{
                        const tableArr=JSON.parse(JSON.stringify(this.initTableData))
                        tableArr.forEach(item =>{
                            item.patternOptions= JSON.parse(JSON.stringify(this.patternOptions))
                        })
                        this.tableData=tableArr
                        this.$refs['formTable'].clearValidate()
                    
                })
         } 
      }) 
    },
    mySelectChange(parentIndex, childIndex, checked){
        this.tableData[parentIndex].table[childIndex].examineId = checked
        //获取已经选择的ids
        const checkIds=this.tableData[parentIndex].table.map(item =>{
                 return item.examineId   
        })

        //下拉框禁选初始化
        this.tableData[parentIndex].patternOptions.forEach((item, index) =>{
              item.disabled=false
        })

        this.tableData[parentIndex].patternOptions.forEach((item, index) =>{
             if(item.examineId === checked){
                 //右侧考核内容添加默认可选值
                 this.tableData[parentIndex].table[childIndex].defaultArr=item.examContentArr
            }
            //下拉框是否可选判断
            checkIds.forEach(vo =>{
                if(item.examineId ===vo){
                     this.$set(this.tableData[parentIndex].patternOptions[index], 'disabled', true)
                }
            })
        })
      
    },
    checkRow(parentIndex, childIndex, title, row){
        if(row.examineId){
             this.checkShowFn('open', row.examineId)
             setTimeout(()=>{
                 this.$refs.examineDialog.initData(parentIndex, childIndex, title, row)
             },100)
            
        }
       
    },
    dialogChange(parentIndex, childIndex, checkArr, unselectedArr){
        this.tableData[parentIndex].table[childIndex].checkArr=checkArr
        const id= this.tableData[parentIndex].table[childIndex].examineId 
         this.tableData.forEach((item , index)=>{
               item.table.forEach((vo, n) =>{
                    if(vo.examineId && vo.examineId === id){
                            vo.showArr=unselectedArr         
                    }
               })
         })
       
    },
    addTableRow(index){
        const obj= {
                examineId:'', 
                ratio:0,
                showArr:[],
                checkArr:[]       
        }
        this.tableData[index].table.push(obj)
    },
    rowDel(parentIndex, childIndex, row){
        const id=this.tableData[parentIndex].table[childIndex].examineId
        this.tableData[parentIndex].patternOptions.forEach((item, index) =>{
            if(item.examineId === id){
                this.$set(this.tableData[parentIndex].patternOptions[index], 'disabled', false)
            }
        })

        if(this.tableData[parentIndex].table[childIndex].checkArr.length){
               this.checkShowFn('del', id, this.tableData[parentIndex].table[childIndex].checkArr)
         } 
        setTimeout(()=>{
             this.tableData[parentIndex].table.splice(childIndex, 1)
             this.$set(this.tableData[parentIndex],'table', this.tableData[parentIndex].table)
        })
       
    },
    //打开选择考查内容弹框 和 删除 的时候对可选数据处理
    checkShowFn(type, id, arr){
        //打开选择考查内容弹框
        if(type === 'open'){
            let showArr=[]
            let checkArrCount=0
            //获取可显示可选择的内容
            this.tableData.forEach((item , index)=>{
                item.table.forEach((vo, n) =>{
                            if(vo.examineId && vo.examineId === id && vo.checkArr.length){
                                    showArr = vo.showArr  
                                    checkArrCount=vo.checkArr.length     
                            }
                    })
            })
            //设置可显示可选择的内容
            this.tableData.forEach((item , index)=>{
                item.table.forEach((vo, n) =>{
                            //当前所属分项考核-存在有已经选择的情况
                            if(showArr.length && vo.examineId && vo.examineId === id){
                                    vo.showArr=showArr     
                                //当前所属分项考核-存在都没选择的情况                                 
                            }else  if(checkArrCount ===0 && showArr.length ===0 && vo.examineId && vo.examineId === id){
                                    vo.showArr=vo.defaultArr
                            //当前所属分项考核-存在都选择完了的情况      
                            }else if((checkArrCount !== 0 &&  vo.defaultArr) && checkArrCount === vo.defaultArr.length && vo.examineId && vo.examineId === id){
                                    vo.showArr=[]
                            }
                    })
           })
            
         //删除table当前row
        }else{
            this.tableData.forEach((item , index)=>{
                item.table.forEach((vo, n) =>{
                            if(vo.examineId && vo.examineId === id){
                                    vo.showArr = vo.showArr.concat(arr)
                            }
                    })
            })
        }

    },
    formSubmit(formName, type){
         this.$refs[formName].validate(valid => {
                if (valid) {
                    const patternList=JSON.parse(JSON.stringify(this.patternOptions))
                    const patternGoalList=JSON.parse(JSON.stringify(this.tableData))
                    const patternGoalArr=[]
                    patternList.forEach(item =>{
                        delete item.disabled
                        delete item.examContentArr
                    })
                    patternGoalList.forEach(item =>{
                        item.table.forEach(vo =>{
                            const namesArr= vo.checkArr.map(v => {return v.name})
                            patternGoalArr.push({
                                examGoalId:item.examGoalId,
                                names:namesArr.join(','),                               
                                examineId:vo.examineId,
                                ratio:vo.ratio
                            })
                        })
                    })
                    
                    const postData={
                        courseId:this.courseId,
                        patternList:patternList,
                        patternGoalList:patternGoalArr
                    }

                   this.userSave(postData);
                }
            });
       
     },
    userSave(postData){
        this.$post(this.api.save(), postData).then( res => {
          if(res.status === 1){
              this.$notify.success({
                  title: 'success',
                  duration:1500,
                  message: res.message
                  })

                  setTimeout(()=>{
                    window.history.back(-1)
                  },200)
                  
                }else{
                    this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.message
                    })
                }  
      })
     }
 }
}
</script>

<style lang='scss' scoped>
 @import '../../styles/courseConstruction.scss';
  .relation_title{
     height:86px;
     border-radius:10px 10px 0px 0px;
     background:url('../../assets/img/bg-dachengdujisuan.png') no-repeat center center;
     background-size:100% 100%;
     line-height:86px;
     text-align:center;  
     font-size:24px;
     font-weight:bold;
     color:rgba(255,255,255,1);
     line-height:86px;
     
 }
 .tick{
    display:inline-block;
    width:18px;
    height:18px;
    background:rgba(3,177,135,1);
    border:1px solid rgba(3,177,135,1);
    border-radius:3px;
    position:relative;
    margin-right:3px;
    &:after{
      content:"\2714";
      display:inline-block;
      color:#fff;
      font-size: 12px;
      margin-left: 4px;
      transform: translateY(-3px); 
    }
 }
 .tips{
   display:inline-block;
   height:21px;
   line-height:21px;
   font-size:14px;
   color:#969696;
    margin-top:20px;
    float:right;
 }
</style>



