<template>
 <div class="exam-content">
    <el-input v-model="itemName" disabled  class="exam-content_disable"></el-input>
    <el-input v-model="data.name" @change="inputTitleChange" placeholder="" class="exam-content_middle"></el-input>
    <el-input v-model="contentName" disabled  class="exam-content_disable"></el-input>
    <el-input v-model="data.examContentnames" @change="inputContentChange" placeholder="考查知识点/题号，用逗号，隔开" class="exam-content_input"></el-input>
    <label  @click.stop="delItem" ><SvgIcon icon-class="iconqudiao" size="20px"></SvgIcon></label>
 </div>
</template>

<script>
export default {
 name: 'ExamContent',
 props: {
   data:{
       type:Object,
       default(){
           return {}
       }
   },
   indexVal:{
       type:Number,
       default:0
   }
 },
 data() {
 return {
   itemName:'分项考核:',
   contentName:'考查内容:'
 }
 },
 created(){},
 mounted(){},
 methods:{
    inputTitleChange(){
        this.$emit('onChange', 'title',  this.indexVal, this.data.name, this.data.examContentnames)
    },
    inputContentChange(){
        this.$emit('onChange', 'content',  this.indexVal,  this.data.name, this.data.examContentnames)
    },
    delItem(){
       this.$emit('onDelete', this.indexVal)
    }
 }
}
</script>

<style lang='scss' scoped>
.exam-content{
    height:40px;
    margin-bottom:10px;
  .exam-content_middle{
      width:300px;
      margin-right:10px;
      float:left;
  }
  .exam-content_disable{
      width:110px;
      margin-right:10px;
      float:left;
  }
  .exam-content_input{
      width:calc(100% - 620px);
      float:left;
      margin-right:7px;
  }
  label{
    display: inline-block;
    line-height: 0;
  }
  .svg-icon{
    color:#969696;
    margin-top:10px;
    cursor:pointer;
  }
}
</style>



