<template>
    <div class="examine-dialog">
        <el-dialog title="选择考查内容"  :visible.sync="dialogFormVisible" :before-close="setCourseCancel">
            <p>分项考核：{{title}}</p>
            <el-form :model="form">
                <el-form-item label="考查内容" >
                    <template>
                        <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
                        <div style="margin: 15px 0;"></div>
                        <el-checkbox-group v-model="checkedItem" @change="handlecheckedItemChange" style="margin-left: 82px;">
                            <el-checkbox v-for="(vo, index) in options" :label="vo.name" :key="index">{{vo.name}}</el-checkbox>
                        </el-checkbox-group>
                    </template>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="setCourseCancel('form')">取 消</el-button>
                <el-button type="primary" @click="setCourseCreate('form')">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
export default {
    name: 'ExamineDialog',
    props: {},
    data(){
        return{
            dialogFormVisible: false,
            title:'',
            parentIndex:0,
            childIndex:0,
            checkAll: false,
            isIndeterminate: true,
            options:[],
            checkedItem: [],
            form: {}
        }
    },
    methods: {
         initData(parentIndex, childIndex, title, row){
            this.title=title
            this.parentIndex=parentIndex
            this.childIndex=childIndex

            const list=row.checkArr.concat(row.showArr) 
            const newList=this.nonrepeatingArray(list) 
            const newArr=[]
            row.defaultArr.forEach(item =>{
                newList.forEach(vo =>{
                    if(item.name ===vo.name){
                            newArr.push(item)
                    }
                })
            })
            
            this.options=newArr
            this.checkedItem=row.checkArr.map(item =>{ return item.name})

            if(this.checkedItem.length && this.checkedItem.length === this.options.length){
                this.isIndeterminate = false
                this.checkAll = true
            }else if (this.checkedItem.length && this.checkedItem.length < this.options.length) {
                this.isIndeterminate = true
                this.checkAll = false
            }else {
                this.isIndeterminate = false
                this.checkAll = false
            }

            this.dialogFormVisible=true;
           
        },
        handleCheckAllChange(val) {
            let arr = []
            this.options.forEach(vo=>{
                arr.push(vo.name)
            })
            this.checkedItem = val ? arr : [];
            this.isIndeterminate = false;
        },
        handlecheckedItemChange(value) {
            let checkedCount = value.length;
            this.checkAll = checkedCount === this.options.length;
            this.isIndeterminate = checkedCount > 0 && checkedCount < this.options.length;
        },
        setCourseCancel(formName) {
            this.dialogFormVisible = false
        },
        setCourseCreate(formName) {   
            const checkArr = []
            const list=JSON.parse(JSON.stringify(this.options))
            //已选择
             this.checkedItem.forEach(vo =>{
                    list.forEach(item =>{
                        if(vo === item.name){
                             item.disabled=true
                             checkArr.push(item)
                        }
                    })
                })
            //未选择
            const unselectedArr=list.reduce((acc,cur)=>{
                 if(!cur.disabled){
                     acc.push(cur)
                     }
                 return acc
            },[])

            this.$emit("success", this.parentIndex, this.childIndex, checkArr, unselectedArr)
            this.setCourseCancel("form")
        },
        //数组去重
        nonrepeatingArray(repeatArray){
            const hash = {};
            return repeatArray.reduce(function(accumulator, currentValue){
                if(!hash[currentValue.name]){
                    hash[currentValue.name]=true;
                    accumulator.push(currentValue)
                }  
                
                return accumulator		   
            
            }, []);
    }
    }
}
</script>
<style lang='scss' scoped>
</style>


